"use client"

import { useState, useEffect } from "react"
import axios from "axios"
import { useAuth } from "@/context/auth-context"

export default function FuelSalesPage() {
  const { user, token } = useAuth()
  const [sales, setSales] = useState([])
  const [fuelTypeId, setFuelTypeId] = useState("")
  const [quantitySold, setQuantitySold] = useState("")
  const [totalAmount, setTotalAmount] = useState("")
  const [message, setMessage] = useState("")

  useEffect(() => {
    if (token) fetchSales()
  }, [token])

  const fetchSales = async () => {
    try {
      const res = await axios.get("/api/fuel-sales", {
        headers: { Authorization: `Bearer ${token}` },
      })
      setSales(res.data)
    } catch (err) {
      console.error(err)
    }
  }

  const handleSubmit = async (e: any) => {
    e.preventDefault()
    try {
      await axios.post(
        "/api/fuel-sales",
        { fuelTypeId, quantitySold, totalAmount },
        { headers: { Authorization: `Bearer ${token}` } }
      )
      setMessage("Sale recorded successfully!")
      setFuelTypeId("")
      setQuantitySold("")
      setTotalAmount("")
      fetchSales()
    } catch (err) {
      setMessage("Failed to add sale")
    }
  }

  return (
    <div className="p-6 max-w-5xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">Fuel Sales</h1>

      <form onSubmit={handleSubmit} className="bg-white shadow-md p-4 rounded-lg mb-6">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <input
            type="text"
            placeholder="Fuel Type ID"
            value={fuelTypeId}
            onChange={(e) => setFuelTypeId(e.target.value)}
            className="p-2 border rounded"
            aria-label="Fuel Type"
          />
          <input
            type="number"
            placeholder="Quantity Sold"
            value={quantitySold}
            onChange={(e) => setQuantitySold(e.target.value)}
            className="p-2 border rounded"
            aria-label="Quantity Sold"
          />
          <input
            type="number"
            placeholder="Total Amount"
            value={totalAmount}
            onChange={(e) => setTotalAmount(e.target.value)}
            className="p-2 border rounded"
            aria-label="Total Amount"
          />
        </div>
        <button
          type="submit"
          className="mt-4 bg-green-600 hover:bg-green-700 text-white py-2 px-4 rounded-lg"
        >
          Add Sale
        </button>
        {message && <p className="mt-2 text-sm text-gray-700">{message}</p>}
      </form>

      <h2 className="text-xl font-semibold mb-3">Your Sales History</h2>
      <table className="w-full bg-white shadow-md rounded-lg">
        <thead className="bg-gray-200 text-left">
          <tr>
            <th className="p-2">Fuel Type</th>
            <th className="p-2">Quantity</th>
            <th className="p-2">Total Amount</th>
            <th className="p-2">Date</th>
          </tr>
        </thead>
        <tbody>
          {sales.map((sale: any) => (
            <tr key={sale.id} className="border-b hover:bg-gray-50">
              <td className="p-2">{sale.fuelTypeId}</td>
              <td className="p-2">{sale.quantitySold}</td>
              <td className="p-2">{sale.totalAmount}</td>
              <td className="p-2">{new Date(sale.saleDate).toLocaleDateString()}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}
